<template>
	<div>
		<mu-dialog :open="dialog" title="" @close="close">
			
			<input type="text" placeholder="" v-model="accesstoken" />
			<mu-flat-button slot="actions" @click="close" primary label="" />
			<mu-flat-button slot="actions" primary label="" @click="toGo()" />
		</mu-dialog>
	</div>
</template>

<script>
	export default {
		data() {
				return {
					dialog: true,
					accesstoken: "",
					name: "",
					head: "",
					cookie: ""
				}
			},
			methods: {
				close() {
					this.dialog = false;
					window.location.href = "#/index/tabs1"
				},
				toGo() {
					var self = this;
					$.ajax({
						url: "https://cnodejs.org/api/v1/accesstoken",
						type: "POST",
						data: {
							accesstoken: self.accesstoken
						},
						//
						success: function(data) {
							console.log(data);
							if(data) {
								self.name = data.loginname;
								self.head = data.avatar_url;

								//cookie
								var date = new Date();
								date.setDate(date.getDate() + 7); //cookie
								self.setCookie('accesstoken', self.accesstoken, date)

								var cookie = self.getCookie('accesstoken')

								self.cookie = cookie;

								setTimeout(function() {
									alert("");
									self.dialog = false;
									window.location.href = "#/index/tabs1";
								}, 500)
							}
						},
						//
						error: function() {
							alert("")
						}
					})
				},
				// cookie cookie
				setCookie(name, val, expires, path) {
					var cookieStr = name + '=' + val;

					if(expires) {
						cookieStr += ';expires=' + expires;
					}

					if(path) {
						cookieStr += ';path=' + path;
					}

					// cookie
					document.cookie = cookieStr;
				},
				// cookie
				getCookie(name) {
					var cookie = document.cookie.split('; ');
					var res;

					for(var i = 0; i < cookie.length; i++) {
						var arr = cookie[i].split('=');
						if(arr[0] === name) {
							res = arr[1];
							break;
						}
					}

					return res;
				}
			}
	}
</script>


